.searchInput{
    padding: 20px;
    border: none;
    text-align: center;
    width: 50%;
    border-radius: 15px;
    margin-bottom: 40px;
    margin-top: 30px;
    background-color: rgb(255, 255, 255);
    /* margin-left: 25%; */
    box-shadow: 0px 5px 25px rgba(97,218,250,0.5);

}
.searchInput::placeholder{
    font-size: 20px;
    font-weight: 400;
    color: rgb(136, 129, 129);
}
.searchInput:focus{
    outline-color:rgb(255, 157, 127);
}